import React from 'react';
import Quagga from 'quagga'; // ES6

import './BarcodeReader.scss';


export default class BarcodeReader extends React.Component {
    constructor(props) {
        super(props)
        this.id = "BarcodeReader__asdasd"

        this.state = {
            result: '',
        }
    }

    componentDidMount() {
        Quagga.init({
            inputStream: {
                name: "Live",
                type: "LiveStream",
                target: document.querySelector(`#${this.id}`),    // Or '#yourElement' (optional)
                constraints: {
                    width: 375,
                    height: 300,
                    facingMode: "environment" // or user
                    // deviceId: "7832475934759384534"
                },
            },
            numOfWorkers: 2,
            frequency: 10,
            decoder: {
                readers : [{
                    format: "code_128_reader",
                    config: {}
                }]
            },
            locator: {
                patchSize: "medium",
                halfSample: true
            },
        }, function (err) {
            if (err) {
                console.log(err);
                return
            }
            console.log("Initialization finished. Ready to start");
            Quagga.start();
        });

        const setResult = (code) => this.setState({ result: code })
        Quagga.onDetected(function(result) {
            var code = result.codeResult.code;
            setResult(code);
        });
        Quagga.onProcessed(function(result) {
            var drawingCtx = Quagga.canvas.ctx.overlay,
                drawingCanvas = Quagga.canvas.dom.overlay;
    
            if (result) {
                if (result.boxes) {
                    drawingCtx.clearRect(0, 0, parseInt(drawingCanvas.getAttribute("width")), parseInt(drawingCanvas.getAttribute("height")));
                    result.boxes.filter(function (box) {
                        return box !== result.box;
                    }).forEach(function (box) {
                        Quagga.ImageDebug.drawPath(box, {x: 0, y: 1}, drawingCtx, {color: "green", lineWidth: 2});
                    });
                }
    
                if (result.box) {
                    Quagga.ImageDebug.drawPath(result.box, {x: 0, y: 1}, drawingCtx, {color: "#00F", lineWidth: 2});
                }
    
                if (result.codeResult && result.codeResult.code) {
                    Quagga.ImageDebug.drawPath(result.line, {x: 'x', y: 'y'}, drawingCtx, {color: 'red', lineWidth: 3});
                }
            }
        });
    }

    render() {
        
        return (
            <>
                <h2 style={{ 
                    gridColumnStart: 1,
                    gridColumnEnd: 3,
                    color: 'blue'
                }} >{this.state.result || '______________'}</h2>
                <div className="BarcodeReader" style={{
                    gridColumnStart: 1,
                    gridColumnEnd: 3,
                }} >
                    <div id={this.id}
                        style={{
                            width: '100%',
                            height: "100px"
                        }}
                    ></div>
                </div>
            </>
        )
    }
}